<template>
	<!-- <YPage :logo="require('@/assets/img/logo_white.png')">
		<div class="s-tool-wrapper">
			
		</div>
	</YPage> -->
	<div class="s-tool-wrapper">
		<input type="file" @change="bindChangeValue" />
		<TButton>确定</TButton></div>
</template>
<script>
	import YPage from '@/components/YPage.vue'
	import { Button as TButton } from 'tdesign-vue';
	export default {
		components: {
			YPage,TButton
		},
		data() {
			return {

			}
		},
		mounted() {
		},
		methods: {
			compressImg(file) {
				var files
				var fileSize = parseFloat(parseInt(file['size']) / 1024 / 1024).toFixed(2)
				var read = new FileReader()
				read.readAsDataURL(file)
				var that = this;
				return new Promise(function(resolve, reject) {
					read.onload = function(e) {
						var img = new Image()
						img.src = e.target.result
						console.log(e.target.result)
						img.onload = function() {
							// 默认按比例压缩
							var w = this.width
							var h = this.height
							// 生成canvas
							var canvas = document.createElement('canvas')
							var ctx = canvas.getContext('2d')
							var base64
							// 创建属性节点
							canvas.setAttribute('width', w)
							canvas.setAttribute('height', h)
							ctx.drawImage(this, 0, 0, w, h)
							base64 = canvas.toDataURL(file['type'], 0.9)
							// 回调函数返回file的值（将base64编码转成file）
							// 1.先转为 blob格式  file.content是此文件的base64格式
							var blob = that.dataURLtoBlob(base64);
							// 拿到文件名
							var fileName = file.name;
							// 2，在转为 file类型
							var file_ys = that.blobToFile(blob, fileName);
							// console.log("file1:",file_ys);
							resolve(file_ys)
						}
					}
				})
			},
			dataURLtoBlob(dataurl) {
				var arr = dataurl.split(','),
					mime = arr[0].match(/:(.*?);/)[1],
					bstr = atob(arr[1]),
					n = bstr.length,
					u8arr = new Uint8Array(n);
				while (n--) {
					u8arr[n] = bstr.charCodeAt(n);
				}
				return new Blob([u8arr], { type: mime });
			},
			blobToFile(theBlob, fileName) {
				theBlob.lastModifiedDate = new Date(); // 文件最后的修改日期
				theBlob.name = fileName; // 文件名
				return new File([theBlob], fileName, { type: theBlob.type, lastModified: Date.now() });
			},
			base64ToFile(urlData, fileName) {
				let arr = urlData.split(",");
				let mime = arr[0].match(/:(.*?);/)[1];
				let bytes = atob(arr[1]); // 解码base64
				let n = bytes.length;
				let ia = new Uint8Array(n);
				while (n--) {
					ia[n] = bytes.charCodeAt(n);
				}
				return new File([ia], `${this.imgData.bucketName}.png`, { type: mime });
			},
			imageToBase64(file) {
				var reader = new FileReader()
				reader.readAsDataURL(file)
				reader.onload = () => {
					console.log('file 转 base64结果：' + reader.result)
					this.iconBase64 = reader.result
				}
				reader.onerror = function(error) {
					console.log('Error: ', error)
				}
			},
			convertImageToBase64(imgUrl) {
				const image = new Image()
				image.crossOrigin = 'anonymous';
				console.log(222)
				return new Promise((resolve, reject) => {
					console.log(333)
					image.src = imgUrl;
					image.onload = () => {
						console.log(444)
						const canvas = document.createElement('canvas')
						const ctx = canvas.getContext('2d')
						canvas.height = image.naturalHeight
						canvas.width = image.naturalWidth
						ctx.drawImage(image, 0, 0)
						const dataUrl = canvas.toDataURL("image/png", 0.8)
						//通过resolve将返回值返回
						console.log(dataUrl)
						resolve(dataUrl)
					}
					image.onerror = (err) => {
						console.log(err)
						return reject(err);
					}
				})

			},
			async bindChangeValue(e) {
				// console.log(e.target)
				// console.log(e.target.files)
				this.imageToBase64(e.target.files[0])
				// let res = await this.compressImg(e.target.files[0])
				// console.log(res)
				// this.imageToBase64(res)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.s-tool-wrapper {}
</style>